import React from 'react';
import Sidebar from './Sidebar';

function CreationDemande() {
    return (
        <div className="w-full h-screen flex">
            <Sidebar/>
            <div className="container mx-auto py-10 h-64 md:w-4/5 w-11/12 px-6">
                <div className="w-full h-full">
                    <h3 className="font-bold text-xl mb-2">Création Demande Client</h3>
                    <div className="bg-white p-8 rounded shadow w-full ">
                        <form action="#" method="POST">
                            <div className="grid grid-cols-2 gap-4">
                                <div>
                                    <label for="firstName" className="block text-sm font-medium text-gray-700">Prénom</label>
                                    <input type="text" id="firstName" name="firstName" className="mt-1 p-2 w-full border rounded-md"/>
                                </div>
                                <div>
                                    <label for="lastName" className="block text-sm font-medium text-gray-700">Nom</label>
                                    <input type="text" id="lastName" name="lastName" className="mt-1 p-2 w-full border rounded-md"/>
                                </div>
                            </div>

                            <div className="mt-4 grid grid-cols-2 gap-4">
                                <div>
                                    <label for="email" className="block text-sm font-medium text-gray-700">Adresse email</label>
                                    <input type="email" id="email" name="email" className="mt-1 p-2 w-full border rounded-md"/>
                                </div>
                                <div>
                                    <label for="phone" className="block text-sm font-medium text-gray-700">Téléphone</label>
                                    <input type="number" id="phone" name="phone" className="mt-1 p-2 w-full border rounded-md"/>
                                </div>
                            </div>

                            <div className="mt-4 flex flex-row items-stretch">
                                <div className="text-sm font-medium text-gray-700 mr-6 self-center">Import CSV</div>
                                <div>
                                    <button type="submit" className="self-center px-3 py-2 bg-blue-200 text-sky-950 rounded-md hover:bg-blue-600">Import Excel</button>
                                </div>
                            </div>
                            <div className="py-4 w-full">
                                <div className="shadow overflow-hidden rounded border-b border-gray-200">
                                    <table className="min-w-full bg-white">
                                    <thead className="bg-gray-800 text-white">
                                        <tr>
                                        <th className="w-1/3 text-left py-3 px-4 uppercase font-semibold text-sm">Info A</th>
                                        <th className="w-1/3 text-left py-3 px-4 uppercase font-semibold text-sm">Info B</th>
                                        <th className="text-left py-3 px-4 uppercase font-semibold text-sm">Info C</th>
                                        <th className="text-left py-3 px-4 uppercase font-semibold text-sm">Info D</th>
                                        </tr>
                                    </thead>
                                    <tbody className="text-gray-700">
                                    <tr>
                                        <td className="w-1/3 text-left py-3 px-4">A définir</td>
                                        <td className="w-1/3 text-left py-3 px-4">A définir</td>
                                        <td className="text-left py-3 px-4"><a className="hover:text-blue-500" href="tel:622322662">A définir</a></td>
                                        <td className="text-left py-3 px-4"><a className="hover:text-blue-500" href="mailto:jonsmith@mail.com">A définir</a></td>
                                    </tr>
                                    <tr className="bg-gray-100">
                                        <td className="w-1/3 text-left py-3 px-4">A définir</td>
                                        <td className="w-1/3 text-left py-3 px-4">A définir</td>
                                        <td className="text-left py-3 px-4"><a className="hover:text-blue-500" href="tel:622322662">A définir</a></td>
                                        <td className="text-left py-3 px-4"><a className="hover:text-blue-500" href="mailto:jonsmith@mail.com">A définir</a></td>
                                    </tr>
                                    </tbody>
                                    </table>
                                </div>
                            </div>

                            <div className="mt-4 grid grid-cols-2 gap-4">
                                <div>
                                    <label for="dropzone-file" className="flex flex-col items-center justify-center border-2 border-gray-300 border-dashed rounded-lg cursor-pointer bg-gray-50 dark:hover:bg-bray-800 dark:bg-gray-700 hover:bg-gray-100 dark:border-gray-600 dark:hover:border-gray-500 dark:hover:bg-gray-600">
                                        <div className="flex flex-col items-center justify-center pt-5 pb-4">
                                            <svg className="w-10 h-10 mb-3 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12"></path></svg>
                                            <p className="mb-2 text-sm text-gray-500 dark:text-gray-400"><span className="font-semibold">Ajouter des documents</span></p>
                                            <p className="text-xs text-gray-500 dark:text-gray-400">SVG, PNG, JPG or GIF (MAX. à définir)</p>
                                            <p className="mt-3 mx-4 text-center text-xs text-gray-500 dark:text-gray-400">Passeport, titre de séjour, CV, diplômes et autres documents</p>
                                        </div>
                                        <input id="dropzone-file" type="file" className="hidden" />
                                    </label>
                                </div> 

                                <div className="">
                                    <textarea
                                    id="comment"
                                    name="comment"
                                    className="w-full px-3 py-2 dark:bg-gray-900 rounded-sm border dark:border-none border-gray-300 focus:outline-none border-solid focus:border-dashed resize-none"
                                    placeholder="Ajouter un commentaire.."
                                    rows="6"
                                    required
                                ></textarea>
                                </div>
                            </div>

                            <div className="mt-6">
                                <button type="submit" className=" p-3 bg-sky-950 text-white rounded-md hover:bg-blue-600">Envoyer Demande</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default CreationDemande;